@import url('https://fonts.googleapis.com/css?family=Exo+2');

body {
   background: -webkit-linear-gradient(45deg, #ECECEC 0, rgba(255,255,255,1) 100%);
   background: -o-linear-gradient(45deg, #ECECEC 0, rgba(255,255,255,1) 100%);
   background: linear-gradient(45deg, #ECECEC 0, rgba(255,255,255,1) 100%);
   font-family: 'Exo 2', sans-serif;
}

button {
	cursor: pointer
}

.recipe-box {
	min-height: 100vh;
	min-width: 100%;
	display: grid;
	justify-items: center;
}

.add-recipe {
	min-width: 70vw;
	margin-top: 5%;
	padding-left: 30px;
}

.add-recipe input {
	width: 40%;
	border: none;
	border-bottom: 0.5px solid #d6d6d6;
	font-size: 30px;
	outline: none;
	background: none;
	color: #232b2b;
	opacity: 0.8;
}

.add-recipe button:focus {
    outline: none;
}

.add-recipe button {
	margin-left: 10px;
	height: 27px;
	font-size: 20px;
	background: none;
	border: 0.5px solid #2aa774;
	border-radius: 10%;
	color: 	#2aa774;
	opacity: 0.5;
}

.recipe-box > ul {
	list-style-type: none;
}

.shown {
	display: block;
}

.hiden {
	display: none;
}

.recipe {
	margin: 15px 0px;
	padding: 10px;
	min-width: 70vw;
	display: grid;
	grid-template-areas: "name-and-btn"
		     			 "ingredients";
    background-color: white;
    -webkit-box-shadow: 0 10px 30px rgba(0,0,0,0.19), 0 2px 6px rgba(0,0,0,0.23);
            box-shadow: 0 10px 30px rgba(0,0,0,0.19), 0 2px 6px rgba(0,0,0,0.23);
    border-radius: 5px;
}

.recipe > .name-and-btn {
	grid-area: "name-and-btn";
	min-width: 100%;
	display: grid;
	grid-template-columns: 0.8fr 1fr 100px;
	-ms-flex-align: center;
	    align-items: center;
}

.recipe .name-and-btn h2 {
	margin-left: 5%;
	color: #232b2b;
}

.recipe > .name-and-btn p {
	font-size: 15px;
	opacity: 0.25;
}

.recipe .name-and-btn button {
	-ms-flex-preferred-size: 100px;
	    flex-basis: 100px;
	color: #970404;
	background: none;
	border: none;
}

.recipe__ingredients {
	grid-area: "ingredients";
}

.recipe__ingredients .name-and-btn {
	display: grid;
	-ms-flex-align: center;
	    align-items: center;
	grid-template-columns: 200px 30px;
}

.recipe__ingredients .name-and-btn button {
	width: 30px;
	height: 30px;
	color: #970404;
	background: none;
	border: none;
}

.recipe__ingredients > li {
	margin-top: 10px;
}

.recipe__ingredients > li p:before {
	 content: '\2014 ';
	 padding-right: 5px; 
	 color: #232b2b;
	 opacity: 0.6;
}

.recipe__ingredients form {
	margin-top: 15px;
}

.recipe__ingredients form input {
	border: none;
	border-bottom: 0.5px solid 	#3b444b;
	outline: none;
	background: none;
	color: #232b2b;
}

.recipe__ingredients form button {
	margin-left: 10px;
	height: 20px;
	width: 50px;
	background: none;
	border: 0.6px solid #2aa774;
	border-radius: 10%;
	color: #2aa774;
}

::-webkit-input-placeholder {
	color: #232b2b;
	opacity: 0.3;
}

:-ms-input-placeholder {
	color: #232b2b;
	opacity: 0.3;
}

::-ms-input-placeholder {
	color: #232b2b;
	opacity: 0.3;
}

::placeholder {
	color: #232b2b;
	opacity: 0.3;
}


